button {
  border: none;
  outline: none;
  background-color: #76c893;
  color: #fff;
  font-size: 14px;
  border-radius: 10px;
  transition: 0.3s all ease-in-out;
}

button:hover {
  transform: scale(1.05);

  background-color: #50cd7e;
}

button:active {
  transform: scale(0.95);
}
